<template>
  <Card>
    <p slot="title" :title="name" style="max-width: 70%;">
      <Icon type="at"></Icon>
      {{name}}
    </p>
    <template slot="extra">
      <a href="javascript:void(0)" v-for="item in topBarItems" :key="item.name" class="cus-card-tbar-btn" v-if="!fromSearch">
        <Icon :type="item.type" color="#9ea7b4" @click.native="btnAction(item.action)"></Icon>
      </a>
      <a v-if="fromSearch" href="javascript:void(0)" @click="btnAction('btnProject')"></a>
      <a v-if="fromSearch" href="javascript:void(0)" @click="btnAction('btnView')"></a>
    </template>
    
    <ul class="cus-list-db">
      <li>
        <span>URL</span>
        <div class="cus-list-right">
          <span class="cus-show-value">{{(method || url )? (method + ' -> ' + url) : ''}}</span>
          <Poptip placement="right" width="300" v-if="!fromSearch">
            <a href="javascript:void(0)"></a>
            <div class="cus-tooltip" slot="content">
              <p class="cus-tooltip-text">URLurl "/" </p>
              <Input placeholder="url" v-model="modifyUrl"></Input>
              <Select v-model="modifyMethod" placeholder="" style="margin-top: 5px;">
                <Option v-for="item in methodList" :value="item.value" :key="item">{{ item.label }}</Option>
              </Select>
              <Button type="primary" size="small" @click.native="submitItem({url: modifyUrl, method: modifyMethod})"></Button>
            </div>
          </Poptip>
        </div>
      </li>
      <li>
        <span></span>
        <div class="cus-list-right">
          <span class="cus-show-value">{{(path || pathEqual )? (path + ' ==> ' + pathEqual) : ''}}</span>
          <Poptip placement="right" width="300" v-if="!fromSearch">
            <a href="javascript:void(0)"></a>
            <div class="cus-tooltip" slot="content">
              <p class="cus-tooltip-text">URL</p>
              <Input placeholder="" v-model="modifyPath"></Input>
              <Input placeholder="" v-model="modifyPathEqual" style="margin-top: 5px;"></Input>
              <Button type="primary" size="small" @click.native="submitItem({path: modifyPath, pathEqual: modifyPathEqual})"></Button>
            </div>
          </Poptip>
        </div>
      </li>
      <li>
        <span></span>
        <div class="cus-list-right">
          <span class="cus-show-value">{{delay || ''}}</span>
          <Poptip placement="right" width="300" v-if="!fromSearch">
            <a href="javascript:void(0)"></a>
            <div class="cus-tooltip" slot="content">
              <p class="cus-tooltip-text">ms</p>
              <div class="cus-tooltip-oneline">
                <Input-number :min="0" placeholder="" size="small" v-model="modifyDelay"></Input-number>
                <Button type="primary" size="small" style="margin: 0" @click.native="submitItem({delay: modifyDelay})"></Button>
              </div>
            </div>
          </Poptip>
        </div>
      </li>
      <li>
        <span></span>
        <div class="cus-list-right">
          <span class="cus-show-value">{{fixedText}}</span>
          <Poptip placement="bottom-end" width="300" v-if="!fromSearch">
            <a href="javascript:void(0)" @click="getSelection"></a>
            <div class="cus-tooltip" slot="content">
              <p class="cus-tooltip-text"></p>
              <div class="cus-radio-group">
                <Radio-group v-model="modifyFixedType" vertical>
                  <Radio label="1"></Radio>
                  <Radio label="2"></Radio>
                  <Radio label="3"></Radio>
                  <Radio label="0"></Radio>
                </Radio-group>
                <div class="selection-list"> 
                  <Select v-model="modifyFixedWrong" placeholder="" class="radio-select"size="small">
                    <Option v-for="item in libList" :value="item._id" :key="item">{{ item.name }}</Option>
                  </Select>
                  <Select v-model="modifyFixedThrow" placeholder="" class="radio-select" size="small">
                    <Option v-for="item in throwList" :value="item.value" :key="item">{{ item.label }}</Option>
                  </Select>
                  <Select v-model="modifyFixedBranch" placeholder="" class="radio-select" size="small">
                    <Option v-for="item in ModelList" :value="item._id" :key="item">{{ item.name }}</Option>
                  </Select>
                </div>
              </div>
              <Button type="primary" size="small" style="margin: 0" @click.native="submitFixData()"></Button>
            </div>
          </Poptip>
        </div>
      </li>
      <!--<li>
        <span></span>
        <div class="cus-list-right">
          <span class="cus-show-value">{{pageNo || ''}}</span>
          <Poptip placement="right" width="300">
            <a href="javascript:void(0)"></a>
            <div class="cus-tooltip" slot="content">
              <p class="cus-tooltip-text"></p>
              <div class="cus-tooltip-oneline">
                <Input-number :min="0" placeholder="" size="small" v-model="modifyPageNo"></Input-number>
                <Button type="primary" size="small" style="margin: 0" @click.native="submitItem({'pageNo': modifyPageNo})"></Button>
              </div>
            </div>
          </Poptip>
        </div>
      </li>-->
      <li>
        <span>API</span>
        <span class="cus-description" :title="description">
          {{description}}
        </span>
      </li>
    </ul>
  </Card>
</template>
<script>
import mixin from '@/mixin/action-api.js'
import { editApiBase, getLib, getApiModel, setApiStatus } from '@/api/api.js'
export default {
  name: 'api-card',
  mixins: [mixin],
  data () {
    return {
      topBarItems: [
        {name: '', type: 'edit', action: 'btnEdit'},
        {name: '', type: 'android-remove-circle', action: 'btnDelete'},
        {name: '', type: 'eye', action: 'btnView'},
      ],
      methodList: [
        {value: 'GET', label: 'GET'},
        {value: 'POST', label: 'POST'},
        {value: 'PUT', label: 'PUT'},
        {value: 'DELETE', label: 'DELETE'},
        {value: 'PATCH', label: 'PATCH'},
      ],
      libList: [],
      ModelList: [],
      throwList: [
        {value: '304', label: '304'},
        {value: '400', label: '400'},
        {value: '404', label: '404'},
        {value: '502', label: '502'},
      ],
      modifyPort: 8080,
      modifyDelay: 0,
      modifyPageNo: 0,
      modifyFixedOutput: '',
      modifyPathEqual: '',
      modifyPath: '',
      modifyFixedType: '',
      modifyFixedWrong: '',
      modifyFixedBranch: '',
      modifyFixedThrow: '',
      modifyUrl: '',
      modifyMethod: '',
      modifyKeys: ['path', 'delay', 'port', 'pageNo', 'pathEqual', 'url', 'method'],
    }
  },
  props: {
    name: {
      type: String,
    },
    id: {
      type: String,
    },
    method: {
      type: String,
    },
    project: {
      type: String,
    },
    url: {
      type: String,
    },
    path: {
      type: String,
    },
    pathEqual: {
      type: String,
    },
    delay: {
      type: Number,
      default: 0,
    },
    fixedOutput: {
      type: Object,
    },
    description: {
      type: String,
    },
    pageNo: {
      type: String,
    },
    fromSearch: {
      type: Boolean,
    },
  },
  watch: {
    'port': function () {
      this.setModifyData()
    },
    'id': function () {
      this.setModifyData()
    },
  },
  computed: {
    proxyText: function () {
      var type = ['', '', '']
      return type[this.proxyType] || ''
    },
    fixedText: function () {
      var type = ['', '', '']
      var ctype = (this.fixedOutput && this.fixedOutput.type !== undefined) ? type[this.fixedOutput.type] : 0
      return ctype ? (ctype + ' ->' + this.fixedOutput.name) : ''
    },
  },
  methods: {
    submitItem (item) {
      item.id = this.id
      editApiBase(item).then(this.afterEdit)
    },
    submitFixData () {
      let type = this.modifyFixedType
      let param = {
        type,
        project: this.project,
        api: this.id,
      }
      param.id = type === '1' ? this.modifyFixedWrong : type === '3' ? this.modifyFixedBranch : undefined
      param.data = type === '2' ? {code: ~~this.modifyFixedThrow} : undefined
      setApiStatus(param).then(this.afterEdit)
    },
    setGData () {
      this.apiInfo.id = this.id
      this.apiInfo.project = this.project
    },
    setModifyData () {
      this.modifyKeys.forEach(key => {
        let mKey = 'modify' + key[0].toUpperCase() + key.slice(1)
        if (this[key] !== undefined) this[mKey] = this[key]
      })
    },
    getSelection () {
      this.getModelList()
      this.getLibList()
    },
    getLibList () {
      let param = {
        pageSize: 1000,
        pageNo: 0,
      }
      getLib(param).then(res => {
        if (!res.code) {
          this.libList = res.data.list
        }
      })
    },
    getModelList () {
      let param = {
        pageSize: 1000,
        pageNo: 0,
        baseid: this.id,
      }
      getApiModel(param).then(res => {
        if (!res.code) {
          this.ModelList = res.data.list
        }
      })
    },
  },
  mounted () {
    this.setGData()
    this.setModifyData()
  }
}
</script>

<style>
.cus-dw-icon {
  margin-right: 5px!important;
}
.cus-list-db li {
  display: flex;
  justify-content: space-between;
}
.cus-tooltip {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.cus-tooltip .ivu-btn {
  margin-top: 10px;
}
.cus-tooltip-text {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  word-break: break-word;
  white-space: normal;
}
.cus-tooltip-oneline {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.cus-tooltip-list {
  width: 100%;
}
.cus-tooltip-list li {
  display: flex;
}
.cus-list-right {
  display: flex;
}
.cus-list-right>span:first-child {
  max-width: 120px;
  overflow: hidden;
  height: 20px;
  margin-right: 5px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.cus-description {
  position: relative;
  max-width: 224px;
  word-break: break-all;
  height: 60px;
  line-height: 20px;
  overflow: hidden;
}
.cus-card-tbar-btn {
  margin-left: 10px;
}
.cus-radio-group {
  display: flex;
  width: 100%;
}
.cus-radio-group .selection-list{
  display: flex;
  flex-direction: column;
}
.cus-radio-group .radio-select{
  display: block;
  margin: 3px 0;
  margin-left: 20px;
}
</style>



