import { deleteApi } from '@/api/api.js'

export default {
  data () {
    return {
      apiInfo: {}
    }
  },
  methods: {
    btnView () {
      this.$router.push({name: 'API', query: { id: this.apiInfo.id, project: this.apiInfo.project }})
    },
    btnProject () {
      this.$router.push({name: '', query: { id: this.apiInfo.project }})
    },
    btnEdit () {
      this.$router.push({name: 'API', query: { id: this.apiInfo.id, project: this.apiInfo.project }})
    },
    btnDelete () {
      this.$Modal.confirm({
        title: 'API',
        content: '<p></p>',
        onOk: () => {
          deleteApi({id: this.apiInfo.id}).then(this.afterEdit)
        },
      })
    },
    afterEdit (data) {
      if (!data.code) {
        this.$Message.success(data.message)
        this.$emit('delete')
      }
    }
  },
}



